@import 'variables/variables';
.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    font-family: sans-serif;
    color: $second-color;
    margin-bottom: 30px;
  }
  form {
    width: 300px;
    .hidden {
      display: none;
    }
    .form-button {
      width: 100px;
      color: white;
      border: 0;
      width: 100%;
      height: 35px;
      font-family: sans-serif;
      font-size: 17px;
      border-radius: 20px;
      background: #0069d9;
    }
  }
}

@media (min-width: 768px) {
  .form-container {
    padding: 50px 0;
  }
  .login {
    border-right: 1px solid $second-color;
  }
}
@media (max-width: 767px) {
  .login-form {
    margin: 25px 0;
  }
  .login {
    border-bottom: 1px solid $second-color;
  }
}
